.product-more__item {
  margin: 0;
  color: @greyish-brown;
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  margin-bottom: 23px;
  text-align: center;
}

@media (min-width: @tablet-width) {
  .product-more__item {
    font-size: 16px;
    max-width: 180px;
    width: 100%;
    margin: 0 auto 85px;
    line-height: 20px;
  }
}

